export default "<template>\n  <div>\n    <NestedDropdown\n      v-model=\"modelData\"\n      :option=\"optionList\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data:()=>({\n    modelData:'',\n    optionList:[\n      {\n        title: \"First Title\",\n        content: [\"First Action\", \"Second Action\"],\n      },\n      {\n        title: \"Second Title\",\n        content: [\"First Content\", \"Second Content\", \"Third Content\"],\n      },\n    ]\n  })\n}\n</script>";